@import "./src/styles/mixins.scss";

.mainBlock {
  @include flexColumn(space-around);
  @include formBlockStyle(360px, 410px);

  .label {
    @include labelForInputStyle();

    .input {
      @include inputStyle();
    }
  }

  .button {
    @include flexRow();
    @include buttonStyle();

  }
}