.screenBackground {
  position: absolute;
  left: 0px;
  top: 0px;

  min-height: 100vh;
  width: 100%;

  background: url(../../assets/background.png) no-repeat center center;
  background-size: cover;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(20px);
  }

}